const DailySpecial = props => {
  const style = {
    background: `url('${props.item.image}')`,
    backgroundSize: 'cover',
    backgroundPosition: 'center center'
  };
  return (
    <div className="right-item">
      <div
        style={style}
        className={'right-item-inner'}
        onClick={() => {
          props.currentItem(props.item, props.index);
        }}
      >
        {/* <img
          src={props.item.image}
          alt={props.item.name}
          title={props.item.name}
        /> */}
        <div className="content">
          {props.item.day === '*Everyday' ? (
            <h5 className="everyday">{props.item.day}</h5>
          ) : (
            <h5>{props.item.day}</h5>
          )}
        </div>
      </div>
    </div>
  );
};

