// export default DailySpecials;

class DailySpecials extends Component {
  constructor(props) {
    super(props);
    this.state = {
      curItem: DailySpecialData[0]
    };
    this.leftItemRef = React.createRef();
    this.rightItemRef = React.createRef();
  }
  componentDidMount() {
    this.rightItemRef.current.children[0].children[0].classList.add('active');
  }

  setCurrentItem = (item, index) => {
    this.setState({ curItem: item });
    if (this.leftItemRef.current)
      this.leftItemRef.current.classList.add('animate');
    setTimeout(() => {
      if (this.leftItemRef.current)
        this.leftItemRef.current.classList.remove('animate');
    }, 1000);
    if (this.rightItemRef.current) {
      this.rightItemRef.current
        .querySelector('.active')
        .classList.remove('active');
      this.rightItemRef.current.children[0].children[index].classList.add(
        'active'
      );
    }
  };
  render() {
    const { curItem } = this.state;
    const leftStyle = {
      background: `url('${curItem.image}') center center / 100% 100% no-repeat`
    };

    return (
      <>
        <h1 className="h1-heading-center mtb-10">Daily Special</h1>
        {/* <div
          style={{
            background: 'url("/images/rose-hr-260.jpg")',
            backgroundSize: 'cover',
            width: '200px',
            height: '24px',
            margin: '0 auto'
          }}
        /> */}
        <div className="seoulContainer daily-specials">
          <div
            style={{ ...leftStyle }}
            className="left"
            ref={this.leftItemRef}
            onTransitionEnd={e => {
              console.log('ended');
            }}
          >
            <p className="left-day">{curItem.day}</p>
            <p className="left-name">{curItem.name}</p>
            <p className="left-desc">{curItem.description}</p>
          </div>
          <div className="right" ref={this.rightItemRef}>
            <div className="right-inner">
              {DailySpecialData.map((item, i) => (
                <DailySpecial
                  key={item.id}
                  currentItem={this.setCurrentItem}
                  item={item}
                  index={i}
                >
                  {item.day}
                </DailySpecial>
              ))}
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default DailySpecials;
